import React, { useEffect, useState } from 'react';
import MainStyles from "./Main.module.css";
import { IoIosArrowRoundDown } from "react-icons/io";
import { Link as ScrollLink } from 'react-scroll';

const MainPage = () => {
  const publicUrl = process.env.PUBLIC_URL;
  const [isScrolled, setIsScrolled] = useState(false);
  const [loaded, setLoaded] = useState(true);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 0);
    };

    // 페이지 로드 시 한 번 실행
    handleScroll();

    // 스크롤 이벤트 리스너 등록
    window.addEventListener('scroll', handleScroll);

    // 컴포넌트 언마운트 시에 이벤트 리스너 제거
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  useEffect(() => {
    // 페이지 로드 시 한 번 실행
    setLoaded(false);
  }, []);

  return (
    <div className={`${MainStyles.wrapper} ${isScrolled || loaded ? MainStyles.loaded : ''}`}>
      <div className={MainStyles.TxtBox}>
      <div className={MainStyles.MainTxt}>
      <h1 className={MainStyles.MainTitleTxt1}>FRONTEND</h1>
      <h1 className={MainStyles.MainTitleTxt2}>KWON JIMIN</h1>
      </div>
      <div  className={MainStyles.SubTxt}>
      <p className={MainStyles.MainSubTxt}>새로운 것을 좋아하며, <strong>도전</strong>을 두려워하지 않는 사람</p>
      <p className={MainStyles.MainSubTxt}>포기하지 않고 다양한 방향으로 <strong>문제 해결</strong>을 위해 노력하는 사람</p>
      <p className={MainStyles.MainSubTxt}>변화하는 <strong>트렌드</strong>에 맞춰 발 빠르게 배우는 권지민 입니다.</p>
      </div>
      </div>
      <p className={MainStyles.LeftTxt}>2024 . PORTFOLIO</p>
      <p className={MainStyles.RightTxt}>Big goals get big results.</p>
      <p className={MainStyles.BottomTxt}>BE BRAVE</p>
      
        
        {/* 수정된 부분 */}
        <ScrollLink to="targetElement" smooth={true} duration={800} className={MainStyles.TxtCircleArrow}>
          <img src={`${publicUrl}/images/textcircle.png`} alt="원형텍스트" className={MainStyles.TxtCircle}/>
          <IoIosArrowRoundDown />
        </ScrollLink>
      </div>
  );
};

export default MainPage; 