import React from 'react';
import ProjectSwiper1 from './ProjectSwiper1';
import ProjectSwiper2 from './ProjectSwiper2';
import ProjectSwiper3 from './ProjectSwiper3';

import ProjectStyles from "./ProjectSwiperStyles.module.css";

const ProjectPage = () => {
  return (
    <div id="targetElement" className={ProjectStyles.wrapper} >
      <div className={ProjectStyles.ProjectMainTxt}>
      <div className={ProjectStyles.MainTxtBox} >
        <h1>project</h1>
        {/* <p>뭐라 써야할지 고민중</p> */}
      </div>
      </div>
      <ProjectSwiper1 />
      <ProjectSwiper2 />
      <ProjectSwiper3 />
    </div>
  );
};

export default ProjectPage ; 