// Header.js
import React, { useEffect, useState } from "react";
import { motion, useCycle } from "framer-motion";
import { MenuToggle } from "./Toggle";
import { Navigation } from "./Nav";
import headerStyles from "./header.module.css";

const sidebar = {
  open: (height = 1000) => ({
    clipPath: `circle(${height * 2 + 200}px at 90% 40px)`,
    transition: {
      type: "spring",
      stiffness: 20,
      restDelta: 2,
    },
  }),
  closed: {
    clipPath: "circle(0px at 92% 80px)",
    transition: {
      delay: 0.5,
      type: "spring",
      stiffness: 400,
      damping: 40,
    },
  },
};

export default function Header() {
  const [isOpen, toggleOpen] = useCycle(false, true);
  const [hasBorder, setHasBorder] = useState(false);
  useEffect(() => {
    const handleScroll = () => {
      // 스크롤 위치가 0 이하이면 border를 없앰, 그 이상이면 border를 추가함
      setHasBorder(window.scrollY > 0);
    };

    // 스크롤 이벤트 리스너 등록
    window.addEventListener('scroll', handleScroll);

    // 컴포넌트 언마운트 시 이벤트 리스너 제거
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);
  return (
    <div className={headerStyles.header}>
      <div className={`${headerStyles.side} ${hasBorder ? headerStyles['border-visible'] : ''}`}>
      <h1 className={headerStyles.title}>P.F</h1>
      <motion.nav
        initial={false}
        animate={isOpen ? "open" : "closed"}
        custom="100%"
      >
        <motion.div className={headerStyles.headernavbar} variants={sidebar} />
        <Navigation />
        <MenuToggle toggle={() => toggleOpen()} />
      </motion.nav>
    </div>
    </div>
  );
}
